import React from "react";
import { Card, Loader, Grid, Dimmer, Segment, Image } from "semantic-ui-react";
import PostCard from "../PostCard/PostCard";

export default function PostFeed({
  posts,
}) {
  return (
    <Card.Group itemsPerRow="1" stackable>

      {posts.map((post) => {
        return (
          <PostCard
            post={post}

          />
        );
      })}
    </Card.Group>
  );
}